body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overscroll-behavior: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.ui.buttons.select-button {
	z-index: 100;
	position: absolute;
	right: 16px;
	bottom: 16px;
}

.select-button.inverted .ui.button {
	margin-left: -1px;
	box-shadow: 0 0 0 1px #ccc inset!important;
}

.ui.menu.type-dropdown {
	z-index: 100;
	position: absolute;
	right: 16px;
	top: 16px;
}

.ui.inverted.menu.type-dropdown .dropdown.item {
	background-color: rgba(0, 0, 0, 0.4);
}

.ui.menu.type-dropdown .item {
	background-color: rgba(0, 0, 0, 0.4);
	padding: 4px 16px;
}

.ui.menu.type-dropdown .dropdown.item  .menu {
	background-color: rgba(0, 0, 0, 0.4);
}

.ui.menu.type-dropdown .ui.dropdown .item,
.ui.menu.type-dropdown .ui.dropdown .menu > .active.item,
.ui.menu.type-dropdown .ui.dropdown .menu > .selected.item {
	background-color: rgba(0, 0, 0, 0.4) !important;
	color: #f0f0f0 !important;
}

.ui.menu.type-dropdown .ui.dropdown .item:hover,
.ui.menu.type-dropdown .ui.dropdown .menu > .active.item:hover,
.ui.menu.type-dropdown .ui.dropdown .menu > .selected.item:hover {
	background-color: rgba(0, 0, 0, 0.2) !important;
	color: #aaa !important;
}

/* popup-dark */
.mapboxgl-popup-content { background: rgba(0, 0, 0, 0.7); color:#fff; padding: 8px 10px; text-align: center; line-height:18px; max-width: 200px; }
.mapboxgl-popup-content span { color:#ccc; font-size: 11px; }
.mapboxgl-popup-anchor-top .mapboxgl-popup-tip,
.mapboxgl-popup-anchor-top-right .mapboxgl-popup-tip,
.mapboxgl-popup-anchor-top-left .mapboxgl-popup-tip { border-bottom-color:rgba(0, 0, 0, 0.7); }
.mapboxgl-popup-anchor-bottom .mapboxgl-popup-tip,
.mapboxgl-popup-anchor-bottom-right .mapboxgl-popup-tip,
.mapboxgl-popup-anchor-bottom-left .mapboxgl-popup-tip { border-top-color:rgba(0, 0, 0, 0.7); }
.mapboxgl-popup-anchor-right .mapboxgl-popup-tip { border-left-color:rgba(0, 0, 0, 0.7); }
.mapboxgl-popup-anchor-left .mapboxgl-popup-tip { border-right-color:rgba(0, 0, 0, 0.7); }

